import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';


import { RegistoFormandoComponent } from './RegistoLogin/registo-formando/registo-formando.component';

import { WorkflowGuard } from './steps/workflow/workflow-guard.service';

const routes: Routes = [

  {
    path: 'painelc',
    loadChildren: 'app/painelc/painelc.module#PainelcModule'
  },
  { path: '',          redirectTo: 'login', pathMatch: 'full' },
  { path: 'registo', component: RegistoFormandoComponent }

];




@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ],
  providers: [WorkflowGuard]
})
export class AppRoutingModule { }



