import { ChangeDetectorRef, Component, OnInit }   from '@angular/core';
import { Router }              from '@angular/router';
import { FormDataService }     from '../data/formData.service';
import { ContactInfo, EnderecoInfo } from '../data/formData.model';
import { WorkflowService } from '../workflow/workflow.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Provincias } from '../../painelc/models/registo-formando.model';
import { Http } from '@angular/http';
import { ScrollService } from '../../shared/scroll.service';


@Component ({
    selector:     'app-mt-wizard-address'
    ,templateUrl: './endereco-info.component.html',
  styleUrls:  ['../stepStyle.css']
})

export class EnderecoInfoComponent implements OnInit {
    title = 'Onde podemos te Lozalizar?';
    enderecoInfo: EnderecoInfo;

  provincias: any;
   distritos:any[]=[];
  cidades= Provincias;


  form:FormGroup;
  endereco: FormControl;
  provincia: FormControl;
  distrito: FormControl;




    constructor(private router: Router, private formDataService: FormDataService, private scrollService: ScrollService,
                private wkFlowService: WorkflowService, private cd: ChangeDetectorRef) {
    }

    ngOnInit() {
      this.scrollService.scrollToTop(this.router);
      this.createFormsControls();

      this.formDataService.getProvinciasJSON().subscribe(
        data => this.provincias=data.places, error => console.log(error));

      this.enderecoInfo = this.formDataService.getEnderecoInfo();
      console.log('Address feature loaded!');
     this.checkAddProv();

      this.form = new FormGroup({
        endereco: this.endereco,
        distrito: this.distrito,
        provincia:this.provincia
      });

    }

    save(form: any): boolean {
        if (!form.valid) {
            return false;
        }

        this.formDataService.setEnderecoInfo(this.enderecoInfo);
        return true;
    }

    goToPrevious(form: any) {
        if (this.save(form)) {
            // Navigate to the SituacaoPro page
            this.router.navigate([this.wkFlowService.getSituacaoProPath()]);
        }
    }

    goToNext(form: any) {
        if (this.save(form)) {
            // Navigate to the resultado-reg page
            this.router.navigate([this.wkFlowService.getContactInfoPath()]);
        }
    }

  createFormsControls(){
    this.endereco= new FormControl ('',[ Validators.required, Validators.minLength(8), Validators.maxLength(60)]);
    this.provincia= new FormControl ('',[ Validators.required]);
    this.distrito= new FormControl ('',[ Validators.required]);

  }
 checkAddProv(){

    if(this.enderecoInfo.provincia===''){
      this.enderecoInfo.provincia='Maputo Provncia';
      this.appendDistritos(this.enderecoInfo.provincia);
    }
 }

  appendDistritos(mkey:string) {
    this.distritos=[];
    this.formDataService.getDistritoJSON(mkey)
      .subscribe(data => {
        if(data !=='') {
          this.distritos.push(data);
          this.cd.detectChanges();
          this.enderecoInfo.distrito = this.distritos[0];
        }

      });

  }
}



