import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';

import { AngularFireModule } from 'angularfire2';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import {
  MatSelectModule,
  MAT_DATE_LOCALE,
  MatButtonModule, MatDatepickerModule, MatNativeDateModule, MatProgressBarModule,
  MatProgressSpinnerModule, MatDialogModule

} from '@angular/material';
import {MatRadioModule} from '@angular/material/radio';
import { AppRoutingModule } from './app.routing';
import { ComponentsModule } from './painelc/components/components.module';

import { AppComponent } from './app.component';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { environment } from '../environments/environment';
import { AuthService } from './shared/auth.service';
import { TitleService } from './shared/title.service';
import { PainelcComponent } from './painelc/painelc.component';
import { PainelcModule } from './painelc/painelc.module';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { LoginComponent } from './RegistoLogin/login/login.component';
import { APP_BASE_HREF } from '@angular/common';
import { RegistoFormandoComponent } from './RegistoLogin/registo-formando/registo-formando.component';
import { DateLimitsService } from './shared/date-limits.service';
import { StepsComponent } from './steps/steps.component';
import { WorkflowService } from './steps/workflow/workflow.service';
import { FormDataService } from './steps/data/formData.service';
import { StepsModule } from './steps/steps.module';
import { ScrollService } from './shared/scroll.service';
import { PdfViewerModule } from 'ng2-pdf-viewer';
import { MyPdfViewerComponent } from './shared/pdfViewer/mypdf-viewer.component';
import { DownloaderService } from './shared/downloader.service';
import { RoundProgressModule } from './shared/round-progressBar/round-progress.module';




@NgModule({
  declarations: [
    AppComponent,
    PainelcComponent,
    StepsComponent,
    PageNotFoundComponent,
    LoginComponent,
    RegistoFormandoComponent,
    MyPdfViewerComponent

  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpModule,
    ReactiveFormsModule,
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    AngularFireModule.initializeApp(environment.firebaseConfig),
    BrowserModule,
    FormsModule,
    HttpModule,
    ComponentsModule,
    MatProgressBarModule,
    MatSelectModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatDialogModule,
    PainelcModule,
    StepsModule,
    RoundProgressModule,
    PdfViewerModule,
    RouterModule,
    AppRoutingModule
  ],
  providers: [
    AuthService, ScrollService,DateLimitsService, TitleService,DownloaderService,
    {provide: MAT_DATE_LOCALE, useValue: 'pt'},
    { provide: FormDataService, useClass: FormDataService },
    { provide: WorkflowService, useClass: WorkflowService }
  ],
  bootstrap: [AppComponent],
  entryComponents:[ MyPdfViewerComponent ]
})
export class AppModule { }



