import {Component, HostBinding, OnInit} from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { Router } from '@angular/router';
import { moveIn, fallIn } from '../../router.animations';
import { AuthService } from '../../shared/auth.service';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import {regexValidators } from '../../shared/Validators/validators';
import { DateAdapter } from '@angular/material';
import { DateLimitsService } from '../../shared/date-limits.service';
import { Nacionalidade, Provincias } from '../../painelc/models/registo-formando.model';

@Component({
  selector: 'app-registo-formando',
  templateUrl: './registo-formando.component.html',
  styleUrls: ['../registo_style.css'],
  animations: [moveIn(), fallIn()]
})
export class RegistoFormandoComponent implements OnInit {
  @HostBinding('@moveIn') moveIn;

 Nacionalidade = [ {Classif: 'Moambicana'}, {Classif:'Estrangeira'}];
  selectedNacio;
  minDate: Date;
  maxDate: Date;
  state = '';
  error: any;
  btnStatus=false;

  form: FormGroup;
  nome: FormControl;
  apelido: FormControl;
  nacionalidade: FormControl;



  private formSumitAttempt: boolean;

  validators= regexValidators;
  provinciasList= Provincias;
  nacionalidadeList= Nacionalidade;
  canChecknome=false;

  constructor(private dateLimServ: DateLimitsService,private formBuilder: FormBuilder,
              public afAuth: AngularFireAuth,public authS: AuthService,private router: Router) {
   /* this.authS.getAuthState().subscribe(user => {
      if(user) {
        this.router.navigateByUrl('/members');
      }
    });*/
   this.selectedNacio='Moambicana';
       this.minDate=dateLimServ.getMinDateD();
       this.maxDate=dateLimServ.getMaxDateD();
  }

  onSubmit(form: FormGroup) {
    this.formSumitAttempt = true;
    if(this.form.valid) {

      this.afAuth.auth.signInWithEmailAndPassword(
        form.value.nome,
        form.value.apelido
      ).then(
        (success) => {
          console.log(success);
          this.router.navigate(['/members']);
        }).catch(
        (err) => {
          console.log(err);
          this.error = err;
        });
    }
  }

  ngOnInit():void {
    this.createFormsControls();
   /* this.authS.getAuthState().subscribe(user => {
      if(user) {
        this.router.navigateByUrl('/painelc/dashboard');
      }else {
        this.router.navigateByUrl('/login');
      }
    });*/
    this.form = new FormGroup({
      nome: this.nome,
      apelido:this.apelido,
      nacionalidade: this.nacionalidade
    });
  }
  hideError(){
    this.error=null;
  }

  createFormsControls(){

    this.nome= new FormControl ('',[ Validators.required, Validators.minLength(2), Validators.maxLength(60)]);

    this.apelido= new FormControl ('',[ Validators.required, Validators.minLength(2), Validators.maxLength(60)]);

    this.nacionalidade= new FormControl ('',[ Validators.required]);
  }




}



