import { ChangeDetectorRef, Component, Input, OnInit }   from '@angular/core';
import { Router }              from '@angular/router';
import { FormDataService }     from '../data/formData.service';
import { ContactInfo } from '../data/formData.model';
import { WorkflowService } from '../workflow/workflow.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Provincias } from '../../painelc/models/registo-formando.model';
import { Http } from '@angular/http';
import { regexValidators } from '../../shared/Validators/validators';
import { ScrollService } from '../../shared/scroll.service';



@Component ({
    selector:     'app-mt-wizard-address'
    ,templateUrl: './contact-info.component.html',
  styleUrls:  ['../stepStyle.css']
})

export class ContactInfoComponent implements OnInit {
    title = 'Como podemos te Contactar?';
    contactInfo: ContactInfo;

    validatorsPattern=regexValidators;
  cellMaxLength=9;

  provincias: any;
   distritos:any[]=[];
  cidades= Provincias;
  select='Maputo';
  selectCid='Maputo';
  selectDis='Maputo';

  form:FormGroup;
  telefone: FormControl;
  telemovel: FormControl;
  telemovelOp: FormControl;
  emaailOp: FormControl;


    constructor(private router: Router, private formDataService: FormDataService,private scrollService: ScrollService,
                private wkFlowService: WorkflowService, private cd: ChangeDetectorRef) {
    }

    ngOnInit() {
      this.scrollService.scrollToTop(this.router);
      this.createFormsControls();

      this.formDataService.getProvinciasJSON().subscribe(
        data => this.provincias=data.places, error => console.log(error));

      this.contactInfo = this.formDataService.getContactInfo();
      console.log('Address feature loaded!');

      this.form = new FormGroup({
        telefone:this.telefone,
        telemovel:this.telemovel,
        telemovelOp:this.telemovelOp,
        emailOp:this.emaailOp
      });

    }

    save(form: any): boolean {
        if (!form.valid) {
            return false;
        }

        this.formDataService.setContactInfo(this.contactInfo);
        return true;
    }

    goToPrevious(form: any) {
        if (this.save(form)) {
            // Navigate to the SituacaoPro page
            this.router.navigate([this.wkFlowService.getEnderecoInfoPath()]);
        }
    }

    goToNext(form: any) {
        if (this.save(form)) {
            // Navigate to the resultado-reg page
            this.router.navigate([this.wkFlowService.getResultadoPath()]);
        }
    }

  createFormsControls(){
     this.telefone= new FormControl ('',[ Validators.minLength(3), Validators.maxLength(9),Validators.pattern(this.validatorsPattern.phone)]);
    this.telemovel= new FormControl ('',[ Validators.required,Validators.minLength(3), Validators.maxLength(9),Validators.pattern(this.validatorsPattern.phone)]);
    this.telemovelOp= new FormControl ('',[ Validators.minLength(3), Validators.maxLength(9),Validators.pattern(this.validatorsPattern.phone)]);
    this.emaailOp= new FormControl ('',[ Validators.pattern(this.validatorsPattern.email)]);

  }


}




