import { EventEmitter, Injectable, Input, Output } from '@angular/core'

@Injectable()
export class TitleService {
  @Output() fire: EventEmitter<any> = new EventEmitter();
  constructor() {
  }

  change(value: string ) {
    console.log('change started');
    this.fire.emit(value);
  }

  getEmittedValue() {
    return this.fire;
  }
}



