import { ChangeDetectorRef, Component, OnInit }   from '@angular/core';
import { Router }              from '@angular/router';

import { Formando }            from '../data/formData.model';
import { FormDataService }     from '../data/formData.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { DateLimitsService } from '../../shared/date-limits.service';
import { Provincias, Nacionalidade, tiposDocId } from '../../painelc/models/registo-formando.model';
import { WorkflowService } from '../workflow/workflow.service';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs/Observable';
import { Http,Headers } from '@angular/http';
import { ScrollService } from '../../shared/scroll.service';
import { DownloaderService } from '../../shared/downloader.service';
import { MatDialog } from '@angular/material';
import { MyPdfViewerComponent } from '../../shared/pdfViewer/mypdf-viewer.component';


@Component ({
    selector:     'app-mt-wizard-personal'
    ,templateUrl: './dados-pessoais.component.html'
    ,  styleUrls:  ['../stepStyle.css']
})

export class DadosPessoaisComponent implements OnInit {
    title = 'Por favor fale-nos sobre voc...';
    formando: Formando;
    mform: any;
    imgUrl='../../../assets/img/account_icon_unitec.png';
    docIdUrl='';

  Nacionalidade = Nacionalidade;

  selectedFotoFile: FileList;

  tipoD=tiposDocId;
  DocIdFilename:string;

  selectedNacio;
  selectedNatur;

  minDate: Date;
  maxDate: Date;
  state = '';
  error: any;
  btnStatus=false;
  url='';

  form: FormGroup;
  fotoFile: FormControl;
    nome: FormControl;
  apelido: FormControl;
  dataNasc: FormControl;
  nacionalidade: FormControl;
  naturalidade: FormControl;
  tipo_Documento_ID: FormControl;
  Nr_Documento_ID: FormControl;
  dataDoc_ID_Emissao: FormControl;
  documento_ID_File: FormControl;

  provincias: any;
  obj:any[];


  constructor(private dateLimServ: DateLimitsService,private router: Router,private  http: Http,public dialog: MatDialog,
              private scrollService: ScrollService,
              private formDataService: FormDataService,private wkFlowService: WorkflowService, private cd: ChangeDetectorRef) {


    this.minDate=dateLimServ.getMinDateD();
    this.maxDate=dateLimServ.getMaxDateD();
    }

    ngOnInit() {

    this.scrollService.scrollToTop(this.router);
      this.formDataService.getProvinciasJSON().subscribe(
        data => this.provincias=data.places, error => console.log(error));

      this.formando = this.formDataService.getFormando();
      console.log('Personal feature loaded!');

      this.checkAddProvAndNacio();

      this.createFormsControls();
      /* this.authS.getAuthState().subscribe(user => {
       if(user) {
       this.router.navigateByUrl('/painelc/dashboard');
       }else {
       this.router.navigateByUrl('/login');
       }
       });*/
      this.form = new FormGroup({
        fotoFile:this.fotoFile,
        nome: this.nome,
        apelido:this.apelido,
        dataNasc:this.dataNasc,
        nacionalidade: this.nacionalidade,
        naturalidade: this.naturalidade,
        tipo_Documento_ID: this.tipo_Documento_ID,
        Nr_Documento_ID: this.Nr_Documento_ID,
        dataDoc_ID_Emissao:this.dataDoc_ID_Emissao,
        documento_ID_File:this.documento_ID_File
      });


    }

  openDialog(): void {
    const dialogRef = this.dialog.open(MyPdfViewerComponent, {
      backdropClass: 'pdfBackdrop',
      panelClass: 'pdfDialogPanel',
      hasBackdrop: true,
      width: '100%',
      data: {  }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
  }
    save(mform: any): boolean {
        if (!mform.valid) {
            return false;
        }

        this.formDataService.setFormado(this.formando);
        return true;
    }

    goToNext(mform: any) {
        if (this.save(mform)) {
            // Navigate to the situcaoPro page
            this.router.navigate([this.wkFlowService.getSituacaoProPath()]);
        }
    }

    readFotoFile(event: any){
  if(event.target.files && event.target.files[0]){
    this.selectedFotoFile=event.target.files;
    const reader= new FileReader();
    reader.onload = (evento: any) => {
      const base64 = evento.target.result;
      this.imgUrl = base64;
      this.formando.fotoFile= this.selectedFotoFile.item(0);
      // const img = base64.split(',')[1];
      // const blob = new Blob([window.atob(img)],{type:'image/*'});
    };
    reader.readAsDataURL(event.target.files[0]);
  }

}

  readDocIdFile(event: any){
    if(event.target.files && event.target.files[0]){
      this.selectedFotoFile=event.target.files;
      const reader= new FileReader();
      reader.onload = (evento: any) => {
        const base64 = evento.target.result;
        this.docIdUrl = base64;
        this.formando.documento_ID_File= this.selectedFotoFile.item(0);
        this.DocIdFilename=this.formando.documento_ID_File.name;
        // const img = base64.split(',')[1];
        // const blob = new Blob([window.atob(img)],{type:'image/*'});
      };
      reader.readAsDataURL(event.target.files[0]);
    }

  }

  /*addFilesIfUserExists(){
    if(){}
  }*/


  createFormsControls(){

      this.fotoFile= new FormControl ();
    this.nome= new FormControl ('',[ Validators.required, Validators.minLength(2), Validators.maxLength(60)]);
    this.apelido= new FormControl ('',[ Validators.required, Validators.minLength(2), Validators.maxLength(60)]);
    this.dataNasc= new FormControl ('',[ Validators.required]);
    this.nacionalidade= new FormControl ('',[ Validators.required]);
    this.naturalidade= new FormControl ('',[ Validators.required]);
    this.tipo_Documento_ID= new FormControl ('',[ Validators.required]);
    this.Nr_Documento_ID= new FormControl ('',[ Validators.required,Validators.minLength(12), Validators.maxLength(14)]);
    this.dataDoc_ID_Emissao= new FormControl ('',[ Validators.required]);
    this.documento_ID_File= new FormControl ('',[ Validators.required]);
  }


  checkAddProvAndNacio(){

    if(this.formando.naturalidade===''){
      this.formando.naturalidade='Maputo Provncia';
    }

    if(this.formando.nacionalidade===''){
      this.formando.nacionalidade='Moambicana';
    }
  }

}



