import {
  AfterViewInit, Component, ElementRef, HostListener, NgZone, OnDestroy, OnInit,
  ViewChild
} from '@angular/core';
import { PdfViewerService } from '../pdf-viewer.service';
import {saveAs as importedSaveAs} from 'file-saver';
import { DownloaderService } from '../downloader.service';
import { Headers, Http, RequestMethod, RequestOptions, Response, ResponseContentType } from '@angular/http';
import { HttpHeaders } from '@angular/common/http';

@Component ({
    selector:     'app-mt-mypdf-viewer'
    ,templateUrl: './mypdf-viewer.component.html',
  styleUrls:  ['./mypdf-viewer.css']
})

export class MyPdfViewerComponent implements OnInit,OnDestroy{

  // @HostListener('window:resize', ['onResize($event)'])

  //@ViewChild('pdfRef', {read: ElementRef}) tref: ElementRef;

  //Round-Progress Values
  current = 0;
  max = 100;
  stroke = 35;
  radius = 125;
  semicircle = false;
  rounded = false;
  responsive = false;
  clockwise = true;
  color = '#4e98b1';
  background = 'rgba(255,255,255,0.3)';
  duration = 800;
  animation = 'easeOutCubic';
  animationDelay = 0;
  animations: string[] = [];
  gradient = false;
  realCurrent = 0;


  titleDoc='';
  filename:any;

  pdfSrc:any='';
  error: any;
  page = 1;
  rotation = 0;
  zoom = 1.0;
  originalSize = false;
  pdf: any;
  renderText = true;
  progressData: any;
  isLoaded = false;
  stickToPage = false;
  showAll = true;
  autoresize = false;
  fitToPage = false;
  isLoading=false;
  hasError=false;
  url='https://doc.lagout.org/programmation/Android/Android%20Programming%20for%20Beginners%20%5BHorton%202016-01-06%5D.pdf';

  //Logic Values for visibility of divs
  IsNumPageVisible=false;
  IsTileDocVisible=false;
  IsZoomDivVisible=false;

  winWidth=window.innerWidth;

  fileSize=0;

  //TimeOut Values For function

  zoomVT:any;
  titleDocVT:any;
  numPagsVT:any;

  private eventOptions: boolean|{capture?: boolean, passive?: boolean};



  constructor(private downloaderServ:DownloaderService,private ngZone: NgZone, private http: Http) {
     this.downloaderServ.downloadBlobFile(this.url).subscribe(
       blob=> {
         this.setBlobSrc(blob);
          this.hasError=false;
         },
        error=>{
         this.error='Erro na Conexo';
         this.hasError=true;
        }
     );
     this.onResizwe();
  }

  ngOnInit(){
  //this.pdfService.resetService();
    this.setResponsiveZoom(this.winWidth);
    if (this.passiveSupported()) { //the implementation of mozilla
      this.eventOptions = {
        capture: true,
        passive: true
      };
    } else {
      this.eventOptions = true;
    }
    this.ngZone.runOutsideAngular(() => {
      window.addEventListener('scroll', this.scroll, <any>this.eventOptions);
    });

  }

  ngOnDestroy() {
    window.removeEventListener('scroll', this.scroll, <any>this.eventOptions);
    //unfortunately the compiler doesn't know yet about this object, so cast to any
  }



  setBlobSrc(blob:Blob){
   this.titleDoc=this.url.substring(this.url.lastIndexOf('/')+1);
    this.pdfSrc=URL.createObjectURL(blob);
    this.fileSize=blob.size;
  }

  setResponsiveZoom(width:number){
    if(width<=588){
      this.zoom=0.25;
     }else if(width>588 && width<=720){
      this.zoom=0.5;
    } else if(width>720 && width<=988){
      this.zoom=0.8;
    }else if(width>988 ){
      this.zoom=1;
    }
  }


  incrementPage(amount: number) {
    this.page += amount;
  }

  incrementZoom(amount: number) {
   let incri=this.zoom;
    incri +=amount;
    if(incri>0.10) {
      this.zoom += amount;
    }
  }

  rotate(angle: number) {
    this.rotation += angle;
  }
  /**
   * Get pdf information after it's loaded
   * @param pdf
   */

  afterLoadComplete(pdf: any) {
    this.pdf = pdf;
    this.isLoaded = true;
  }
  /**
   * Handle error callback
   *
   * @param error
   */
  onError(error: any) {
    this.error = 'Erro na Conexo'; // set error
    this.hasError=true;
  }

  /**
   * Pdf loading progress callback
   * @param {PDFProgressData} progressData
   */

  onProgress(progressData: any) {
    this.progressData = progressData;
   const value= this.getInt(this.progressData.loaded / this.fileSize * 100);
   if(value>=0 && value<=100) {
     this.current =value;
   }
    this.isLoaded = false;
    this.error = null; // clear error
    this.hasError=false;
  }

  getInt(value: number){
    return Math.round(value);
  }


  scroll = (): void => {
     this.ngZone.run(() => {
       this.IsNumPageVisible=true;
       this.changeNumPageVisibilityValue();
      });

  }


  onResizwe(){
    window.onresize = (e) =>
    {
      this.ngZone.run(() => {
       this.setResponsiveZoom(window.innerWidth);
      });
    };
  }
  passiveSupported(): boolean{

     let passiveSupported = false;

     try {
          const options = Object.defineProperty({}, 'passive', {
          get: function() {
           passiveSupported = true;
      }
       });

       window.addEventListener('test', null, options);
        } catch(err) {}
       return passiveSupported;
     }

    changeNumPageVisibilityValue() {
    clearTimeout(this.numPagsVT);
    this.numPagsVT = setTimeout(() => {
      this.IsNumPageVisible=false;
      }, 1000);
    }

    changeZoomDivVisibilityValue() {
     clearTimeout(this.zoomVT);
     this.zoomVT = setTimeout(() => {
      this.IsZoomDivVisible=false;
      }, 2000);
   }

     changeTitleVisibilityValue() {
        clearTimeout(this.titleDocVT);
       this.titleDocVT = setTimeout(() => {
       this.IsTileDocVisible=false;
     }, 1000);
    }

    showZoomDiv(){
      this.IsZoomDivVisible=true;
    }

}




