import { Component, OnInit, Input }   from '@angular/core';

import { FormData }                   from '../data/formData.model';
import { FormDataService }            from '../data/formData.service';

@Component ({
    selector:     'app-mt-wizard-resultado'
    ,templateUrl: './resultado-reg.component.html',
  styleUrls:  ['../stepStyle.css']
})

export class ResultadoRegComponent implements OnInit {
    title = 'Obrigado por ter ficado atento!!!';
    @Input() formData: FormData;
    isFormValid = false;
     imgUrl='../../../assets/img/account_icon_unitec.png';

    constructor(private formDataService: FormDataService) {
    }

    ngOnInit() {
        this.formData = this.formDataService.getFormData();
        this.isFormValid = this.formDataService.isFormValid();
      this.readFoto();
        console.log('Result feature loaded!');
    }

    submit() {
        alert('Excellent Job!');
        this.formData = this.formDataService.resetFormData();
        this.isFormValid = false;
    }

  readFoto(){
      if(this.formData.fotoFile) {
        console.log('OKKK');
       const reader = new FileReader();

        reader.onload = (evento: any) => {
          this.imgUrl = evento.target.result;
          console.log( evento.target.result);
         // console.log( evento.target.result.split(',')[0]);
        };

        reader.readAsDataURL(this.formData.fotoFile);
      }
  }


}



