import { Injectable, Output } from '@angular/core';

@Injectable()
export class DateLimitsService {
  minDate: Date;
  maxDate: Date;
  sDate= new Date();
  yearInt=0;
  constructor() {

   this.yearInt= this.sDate.getFullYear();
    this.maxDate=new Date(this.yearInt,12,0);
    this.minDate=new Date(this.yearInt-7,0,1);
  }

  getMinDateD(){
    return this.minDate;
  }
  getMaxDateD(){
    return this.maxDate;
  }
}




