import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';
import { UserProfileComponent } from './user-profile/user-profile.component';
import { MperfilComponent } from './mperfil.component';
import { EditProfileComponent } from './user-profile/edit-profile/edit-profile.component';
import { PainelcComponent } from '../painelc.component';
import { AppComponent } from '../../app.component';


const mpRoutes: Routes = [
  {
    path: 'u',   component: MperfilComponent,
     children: [
       { path: 'perfil', component: UserProfileComponent },
    { path: 'perfil/editar-perfil', component: EditProfileComponent },
    { path: '', redirectTo: 'perfil', pathMatch: 'full' }
  ]}
  ];


@NgModule({
  imports: [
    // RouterModule.forChild(mpRoutes)
  ],
  exports: [
RouterModule
  ]
})
export class MperfilRoutingModule { }



