import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';
import { CursosComponent } from './cursos/cursos.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ModulosCursosComponent } from './cursos/modulos/modulos.component';
import { TableListComponent } from './table-list/table-list.component';
import { TypographyComponent } from './typography/typography.component';
import { IconsComponent } from './icons/icons.component';
import { MapsComponent } from './maps/maps.component';
import { NotificationsComponent } from './notifications/notifications.component';
import { UpgradeComponent } from './upgrade/upgrade.component';
import { PainelcComponent } from './painelc.component';
import { PageNotFoundComponent } from '../page-not-found/page-not-found.component';
import { LoginComponent } from '../RegistoLogin/login/login.component';
import { UserProfileComponent } from './mperfil/user-profile/user-profile.component';
import { EditProfileComponent } from './mperfil/user-profile/edit-profile/edit-profile.component';
import { MperfilComponent } from './mperfil/mperfil.component';
import { RegistoFormandoComponent } from '../RegistoLogin/registo-formando/registo-formando.component';
import { StepsComponent } from '../steps/steps.component';
import { WorkflowGuard } from '../steps/workflow/workflow-guard.service';
import { DadosPessoaisComponent } from '../steps/dadosPesssoais/dados-pessoais.component';
import { SituacaoProComponent } from '../steps/situacaoPro/situacao-pro.component';
import { ContactInfoComponent } from '../steps/contactInfo/contact-info.component';
import { ResultadoRegComponent } from '../steps/resultado-reg/resultado-reg.component';
import { EnderecoInfoComponent } from '../steps/enderecoInfo/endereco-info.component';

const pcRoutes: Routes = [
  {
    path: 'painelc', component: PainelcComponent,
    children: [
      { path: 'dashboard', component: DashboardComponent },
      {
        path: 'u',component: MperfilComponent,
        children: [
          { path: 'perfil', component: UserProfileComponent },
          { path: 'perfil/editar-perfil', component: EditProfileComponent },
          { path: '', redirectTo: 'perfil', pathMatch: 'full' }
        ]
      },
      { path: 'cursos', component: CursosComponent },
      { path: 'modulos', component: ModulosCursosComponent },
      { path: 'table-list', component: TableListComponent },
      { path: 'typography', component: TypographyComponent },
      { path: 'icons', component: IconsComponent },
      { path: 'maps', component: MapsComponent },
      { path: 'notifications', component: NotificationsComponent },
      { path: 'upgrade', component: UpgradeComponent },
      { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
      {
        path: 'notFound',
        component: PageNotFoundComponent
      },
      {
        path: '**',          redirectTo: 'notFound', pathMatch: 'full'
      }
    ]
  },
  { path: 'registo-formando',  component: StepsComponent,
    children:[
      // 1st Route
      { path: 'dados-pessoais',  component: DadosPessoaisComponent },
      // 2nd Route
      { path: 'situacao-pro',  component: SituacaoProComponent, canActivate: [WorkflowGuard] },
      // 3rd Route
      { path: 'endereco-info',  component: EnderecoInfoComponent, canActivate: [WorkflowGuard] },
      // 4th Route
      { path: 'contact-info',  component: ContactInfoComponent, canActivate: [WorkflowGuard] },
      // 5th Route
      { path: 'resultado-reg',  component: ResultadoRegComponent, canActivate: [WorkflowGuard] },
      // 6th Route
      { path: '',   redirectTo: '/registo-formando/dados-pessoais', pathMatch: 'full' }
    ]
  },
  { path: 'registo', component: RegistoFormandoComponent },
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  {
    path:'login', component: LoginComponent
  },
  {
    path: 'notFound',
    component: PageNotFoundComponent
  },
  {
    path: '**',          redirectTo: 'notFound', pathMatch: 'full'
  }
];
@NgModule({
  imports: [
    RouterModule.forChild(pcRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class PainelcRoutingModule { }



