import { Component, OnInit }   from '@angular/core';
import { NavigationEnd, Router }              from '@angular/router';

import { FormDataService }     from '../data/formData.service';
import {
  areas_interesseList, nivelFormacaoList,
  situacoesProfissionais
} from '../../painelc/models/registo-formando.model';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Ocupacao, SituacaoPro } from '../data/formData.model';
import { WorkflowService } from '../workflow/workflow.service';
import { Observable } from 'rxjs/Observable';
import { ScrollService } from '../../shared/scroll.service';

@Component ({
    selector:     'app-mt-wizard-work'
    ,templateUrl: './situacao-pro.component.html',
  styleUrls:  ['../stepStyle.css']
})

export class SituacaoProComponent implements OnInit {

  dataNasc: FormControl;
  title = 'Qual  a tua Situao Profissional?';
    ocupacaoFormando: Ocupacao;


    form: FormGroup;

    situacaoPro: SituacaoPro;

    sitProfissionais= situacoesProfissionais;
    areas_interessesL: any;
    nivel_formacaoL= nivelFormacaoList;

    tipoSituacaoPro: FormControl;
    empresa: FormControl;
    cargo_empresa: FormControl;
    nivel_formacao: FormControl;
    areas_interrese: FormArray;
    cvLink: FormControl;
    hasJob=false;



    constructor(private router: Router, private formDataService: FormDataService
               ,private wkFlowService: WorkflowService, private scrollService: ScrollService) {
    }

    ngOnInit() {
      this.scrollService.scrollToTop(this.router);
      this.createFormsControls();
        this.situacaoPro = this.formDataService.getSituacaoPro();
        console.log('Work feature loaded!');
       this.checkHasJob(this.situacaoPro.tipoSituacaoPro);
      this.createForm();
      setTimeout((res) => {
        this.areas_interessesL = areas_interesseList;
        this.initCheckboxes();
      });

    }

    save(form: any): boolean {
        if (!form.valid) {
            return false;
        }

        this.formDataService.setSituacaoPro(this.situacaoPro);
        return true;
    }

    goToPrevious(form: any) {
        if (this.save(form)) {
            // Navigate to the dadosPesssoais page
            this.router.navigate([this.wkFlowService.getDadospPath()]);
        }
    }

    goToNext(form: any) {
        if (this.save(form)) {
            // Navigate to the contactInfo page
            this.router.navigate([this.wkFlowService.getEnderecoInfoPath()]);
        }
    }

  createFormsControls(){

    this.tipoSituacaoPro= new FormControl (null,[ Validators.required]);
    this.nivel_formacao= new FormControl (null,[ Validators.required]);
    this.empresa= new FormControl (null,[ Validators.required]);
    this.cargo_empresa= new FormControl (null,[ Validators.required]);
    this.areas_interrese= new FormArray([]);

  }

  createForm(){
    if(this.hasJob) {
      this.form = new FormGroup({
        tipoSituacaoPro: this.tipoSituacaoPro,
        nivel_formacao: this.nivel_formacao,
        empresa: this.empresa,
        cargo_empresa: this.cargo_empresa,
        areas_interesse:this.areas_interrese
      });
    }else{
      this.form = new FormGroup({
        tipoSituacaoPro: this.tipoSituacaoPro,
        nivel_formacao: this.nivel_formacao,
        areas_interesse:this.areas_interrese
      });
    }
  }

  checkHasJob(value: string){
    if(value==='Emprego Efetivo'){
      this.hasJob=true;
      this.createForm();
    }else{
      this.hasJob=false;
      this.createForm();
    }
  }

  onChange(event) {
    const interests = <FormArray>this.areas_interrese as FormArray;

    if(event.checked) {
     const fc=new FormControl(event.source.value);
      interests.push(fc);
      this.situacaoPro.areas_interrese.push(fc.value);
      this.changeCheckboxStatus();
    } else {
      const fc=new FormControl(event.source.value);
      this.unCheckedUnselected(fc.value);
      const i = interests.controls.findIndex(x => x.value === event.source.value);
      interests.removeAt(i);
      this.situacaoPro.areas_interrese.splice(i,1);
      this.changeCheckboxStatus();
    }

  }

  changeCheckboxStatus(){
    const interests = <FormArray>this.areas_interrese as FormArray;

    if( this.situacaoPro.areas_interrese.length>=1) {

      for(let i=0; i<this.situacaoPro.areas_interrese.length;i++) {

        for(let k=0;k<this.areas_interessesL.length;k++){
          if(this.situacaoPro.areas_interrese[i]===this.areas_interessesL[k].descricao){
            this.areas_interessesL[k].isChecked=true;
          }
        }
      }
    }
  }

  unCheckedUnselected(value: string){

    for(let k=0;k<this.areas_interessesL.length;k++) {
      if(this.areas_interessesL[k].descricao===value) {
        this.areas_interessesL[k].isChecked = false;
      }

    }
  }
  initCheckboxes(){
    const interests = <FormArray>this.areas_interrese as FormArray;

    if( this.situacaoPro.areas_interrese.length>=1) {

      for(let i=0; i<this.situacaoPro.areas_interrese.length;i++) {

        for(let k=0;k<this.areas_interessesL.length;k++){
          if(this.situacaoPro.areas_interrese[i]===this.areas_interessesL[k].descricao){
           this.areas_interessesL[k].isChecked=true;
          }
        }
        const fc = new FormControl(this.situacaoPro.areas_interrese[i]);
        interests.push(fc);
      }
    }

  }


}



