import { Component, HostBinding, OnInit } from '@angular/core'
import { AngularFireAuth} from 'angularfire2/auth';
import { Router } from '@angular/router'
import { AuthService } from '../../shared/auth.service'
import { Observable } from 'rxjs/Observable'
import { AngularFireDatabase} from 'angularfire2/database'
import { fallIn, moveIn, moveInLeft } from '../../router.animations'

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css'],
  animations: [moveIn(), fallIn(), moveInLeft()]
})
export class ChatComponent implements OnInit {
  @HostBinding('@moveIn') moveIn;
  items: Observable<any[]>;
  name: any;
  msgVal = '';
  msgRef: any;

  constructor(public afAuth: AngularFireAuth, public router: Router,
              public authS: AuthService, db: AngularFireDatabase) {
    this.items = db.list('/messages', ref => ref.orderByChild('messages').limitToLast(5)).valueChanges();
      this.msgRef = db.list('/messages');
    this.authS.getAuthState().subscribe(user => {
      if (user) {
        this.name = user;
      }
    });
  }

  logout() {
    this.afAuth.auth.signOut();
    this.router.navigateByUrl('/login');
  }



  chatSend(theirMessage: string) {
    this.msgRef.push({ message: theirMessage, name: this.name.displayName});
    this.msgVal = '';
  }

  ngOnInit() {
  }
}



