import { NgModule }           from '@angular/core';
import { BrowserModule }      from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule }        from '@angular/forms';

/* App Root */
import { NavbarComponent }    from './navbar/navbar.component';

/* Feature Components */
import { DadosPessoaisComponent } from './dadosPesssoais/dados-pessoais.component';
import { SituacaoProComponent } from './situacaoPro/situacao-pro.component';
import { ContactInfoComponent } from './contactInfo/contact-info.component';
import { ResultadoRegComponent } from './resultado-reg/resultado-reg.component';
/* Routing Module */

import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import {
  MatCheckboxModule,
  MatDatepickerModule, MatDialogModule, MatNativeDateModule, MatProgressBarModule, MatProgressSpinnerModule,
  MatRadioModule,
  MatSelectModule
} from '@angular/material';
import { HttpModule } from '@angular/http';
import { EnderecoInfoComponent } from './enderecoInfo/endereco-info.component';
import { PdfViewerModule } from 'ng2-pdf-viewer';
import { MyPdfViewerComponent } from '../shared/pdfViewer/mypdf-viewer.component';



@NgModule({
    imports:      [
     BrowserModule,
      CommonModule,
      FormsModule,
      ReactiveFormsModule,
      MatProgressBarModule,
      MatSelectModule,
      MatDatepickerModule,
      MatNativeDateModule,
      MatProgressSpinnerModule,
      MatRadioModule,
      MatCheckboxModule,
      MatDialogModule,
       RouterModule
],
    declarations: [
      NavbarComponent,
      DadosPessoaisComponent,
      SituacaoProComponent,
      EnderecoInfoComponent,
      ContactInfoComponent,
      ResultadoRegComponent ],
  exports: [
    NavbarComponent]
})

export class StepsModule {}



